html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-inter);
  background-color: var(--dark-bg);
  color: var(--dark-text2);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.25rem;
}
@media (min-width: 71.875em) {
  h2 {
    font-size: 1.5rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-text1: hsl(228, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
}

:root {
  --font-inter: "Inter", sans-serif;
}

.container {
  max-width: 69.375rem;
  padding: 0 1.5625rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.card {
  align-self: center;
  background-color: var(--dark-card);
  color: var(--dark-text1);
  width: 15.9375rem;
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.25rem;
}
.card--facebook {
  border-top: 4px solid var(--facebook);
}
.card--twitter {
  border-top: 4px solid var(--twitter);
}
.card--instagram {
  border-top: 4px solid transparent;
  border-image: linear-gradient(to right, var(--instagram-start), var(--instagram-end)) 1;
}
.card--youtube {
  border-top: 4px solid var(--youtube);
}
.card__platform {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
}
.card__username {
  font-size: 0.75rem;
  font-weight: 700;
  margin: 0;
}
.card__count {
  margin: 1.5rem;
}
.card__count--big {
  font-size: 3.125rem;
  font-weight: 700;
  color: var(--dark-text2);
}
.card__label {
  text-transform: uppercase;
  letter-spacing: 4px;
  font-weight: 300;
  font-size: 0.875rem;
}
.card__change span {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.25rem;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}

.toggle {
  border: none;
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  gap: 0.3rem 0.5rem;
  margin: 0;
}
.toggle label {
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dark] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=system] {
  align-self: flex-start;
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
}
.toggle label[for=light] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  margin: 0.125rem -0.125rem;
  opacity: 0%;
  width: 1.25rem;
  height: 1.25rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 1px solid var(--dark-text2);
}
.toggle__background {
  display: block;
  height: 100%;
  width: 100%;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  border-radius: 0.75rem;
  overflow: hidden;
  position: absolute;
  top: 0;
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  margin: -0.0625rem 0;
  left: 0;
  right: 100%;
  width: 1.25rem;
  height: 1.25rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  top: 0.1875rem;
  transition: all 150ms ease-in-out;
}
.toggle #light:checked ~ .toggle__button {
  right: 0.1875rem;
  left: calc(100% - 1.25rem);
}
.toggle #system:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.header {
  margin-top: 2.25rem;
  display: flex;
  flex-wrap: wrap;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  margin: 0;
  padding: 0;
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--dark-text1);
}

.cards {
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  justify-content: center;
}
@media (min-width: 40em) {
  .cards {
    display: grid;
    grid-template-columns: repeat(2, 255px);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 255px);
  }
}

.small-card {
  align-self: center;
  background-color: var(--dark-card);
  color: var(--dark-text1);
  width: 15.9375rem;
  text-align: center;
  padding: 1.5625rem;
  border-radius: 0.25rem;
}
.small-card__stats {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-top: 1.25rem;
}
.small-card__count {
  font-size: 2.25rem;
  font-weight: 700;
  color: var(--dark-text2);
  line-height: 2.125rem;
}
.small-card__platform {
  display: flex;
  align-items: center;
  justify-content: space-between;
}