html {
    font-size: 100%;
    box-sizing: border-box;
    font-family: var(--font-inter);
    background-color: var(--dark-bg);
    color: var(--dark-text2);
}

*, 
*::before, 
*::after {
    box-sizing: inherit;
}

body {
    margin: 0;
    padding: 0;
    font-family: var(--font-inter);
    background-color: var(--dark-bg);
    color: var(--dark-text2);
}